<template>
  <div id="app" ref="appContainer">
    <div class="container">
      <aside class="sidebar">
        <h2>メニュー</h2>
        <ul>
          <li><a href="/bector">ベクトルと行列</a></li>
          <li><a href="/calc">行列の四則演算</a></li>
          <li><a href="/">固有値と固有ベクトル</a></li>
          <li><a href="/diagonalization">行列の対角化</a></li>
          <li><a href="/application">行列の応用</a></li>
        </ul>
      </aside>

      <main class="main-content">
        <h1>{{ learningContent.title }}</h1>
        <div v-html="learningContent.mainText"></div>

        <div v-if="learningContent.examples && learningContent.examples.length > 0" class="example-section">
          <h3>例題</h3>
          <div v-for="example in learningContent.examples" :key="example.id" class="example-card">
            <p>{{ example.problemText }}</p>
            <div class="matrix-container" v-html="example.matrix"></div>
            
            <div class="explanation">
              <div v-html="example.solution"></div>
            </div>
          </div>
        </div>
      </main>

      <aside class="quiz-area">
        <div v-for="(quiz, index) in learningContent.quizzes" :key="quiz.id" class="quiz-card">
          
          <h2>{{ `Q${index + 1}. ${quiz.questionText}` }}</h2>
          
          <div class="matrix-container" v-html="quiz.matrix"></div>

          <div class="options">
            <div v-for="option in quiz.options" :key="option.id" class="option">
              <input type="radio" :id="`q${quiz.id}_o${option.id}`" :name="`quiz_${quiz.id}`" :value="option.id" v-model="selectedAnswers[quiz.id]">
              <label :for="`q${quiz.id}_o${option.id}`" v-html="option.text"></label>
            </div>
          </div>

          <button @click="revealAnswer(quiz.id)" class="answer-button">解答を見る</button>

          <div v-if="revealedAnswers[quiz.id]" class="explanation">
            <div v-html="quiz.explanation"></div>
          </div>
        </div>
      </aside>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, onUpdated } from 'vue';

const learningContent = ref({
  title: '固有値と固有ベクトル',
  mainText: `
    <p>
      行列は、ベクトルを別のベクトルに変換する「関数」のようなものだと考えることができます。
      多くの場合、行列 \\(A\\) をベクトル \\(\\vec{x}\\) に作用させると、\\(\\vec{x}\\) は向きも大きさも変わってしまいます。
    </p>
    <p>
      でも、中には特別なベクトルがあって、行列 \\(A\\) を作用させても<strong>向きが変わらず、大きさだけが変わる</strong>ものがあります。
      この特別なベクトルを<strong>固有ベクトル</strong>、その時の大きさの変化率（スカラー倍）を<strong>固有値</strong>と呼びます。
    </p>
    <p>
      数式で書くと、以下のようになります。これが固有値・固有ベクトルの定義式です。
    </p>
    <div class="formula">
      $$ A\\vec{x} = \\lambda\\vec{x} $$
    </div>
    <p>
      ここで、
      <ul>
        <li>\\(A\\): \\(n \\times n\\) の正方行列</li>
        <li>\\(\\vec{x}\\): 固有ベクトル (ゼロベクトルではない)</li>
        <li>\\(\\lambda\\): 固有値 (スカラー)</li>
      </ul>
    </p>
    <h3>固有値の求め方</h3>
    <p>
      固有値は、次の<strong>固有多項式</strong>（または特性方程式）を解くことで求められます。
    </p>
    <div class="formula">
      $$ \\det(A - \\lambda I) = 0 $$
    </div>
    <p>
      \\(I\\) は単位行列です。この方程式を解いて得られた \\(\\lambda\\) が、行列 \\(A\\) の固有値となります。
    </p>
  `,
  examples: [
    {
      id: 1,
      problemText: '例題：次の行列の固有値を求めてみよう。',
      matrix: `$$ A = \\begin{pmatrix} 5 & -1 \\\\ 3 & 1 \\end{pmatrix} $$`,
      solution: `
        <p>固有多項式 \\(\\det(A - \\lambda I) = 0\\) を計算します。</p>
        <p>$$ \\det \\begin{pmatrix} 5-\\lambda & -1 \\\\ 3 & 1-\\lambda \\end{pmatrix} = 0 $$</p>
        <p>
          行列式を計算すると、<br>
          \\((5-\\lambda)(1-\\lambda) - (-1) \\cdot 3 = 0\\) <br>
          \\(5 - 5\\lambda - \\lambda + \\lambda^2 + 3 = 0\\) <br>
          \\(\\lambda^2 - 6\\lambda + 8 = 0\\) <br>
          \\((\\lambda - 2)(\\lambda - 4) = 0\\)
        </p>
        <p>したがって、固有値は \\(\\lambda = 2, 4\\) となります。</p>
      `
    }
  ],
  quizzes: [
    {
      id: 1,
      questionText: '次の行列 \\(A\\) の固有値を求めてください。',
      matrix: `$$
                \\begin{bmatrix} 4 & 1 \\\\ 3 & 2 \\end{bmatrix}
              $$`,
      options: [
        { id: 1, text: '\\(\\lambda = 1, 5\\)' }, { id: 2, text: '\\(\\lambda = 2, 3\\)' },
        { id: 3, text: '\\(\\lambda = -1, -5\\)' }, { id: 4, text: '\\(\\lambda = 4, 2\\)' },
      ],
      correctAnswerId: 1,
      explanation: `
        <p><strong>正解は \\(\\lambda = 1, 5\\) です。</strong></p>
        <p>固有多項式 \\(\\det(A - \\lambda I) = 0\\) を計算します。</p>
        <p>$$ \\det \\begin{pmatrix} 4-\\lambda & 1 \\\\ 3 & 2-\\lambda \\end{pmatrix} = 0 $$</p>
        <p>
          行列式を計算すると、<br>
          \\((4-\\lambda)(2-\\lambda) - 1 \\cdot 3 = 0\\) <br>
          \\(\\lambda^2 - 6\\lambda + 5 = 0\\) <br>
          \\((\\lambda - 1)(\\lambda - 5) = 0\\)
        </p>
        <p>したがって、固有値は \\(\\lambda = 1, 5\\) となります。</p>
      `
    },
    {
      id: 2,
      questionText: '次の行列 B の固有値を求めてください。',
      matrix: `$$
                \\begin{bmatrix} 2 & -1 \\\\ -2 & 3 \\end{bmatrix}
              $$`,
      options: [
        { id: 1, text: '\\(\\lambda = 2, 3\\)' }, { id: 2, text: '\\(\\lambda = -2, -3\\)' },
        { id: 3, text: '\\(\\lambda = 1, 4\\)' }, { id: 4, text: '\\(\\lambda = -1, -4\\)' },
      ],
      correctAnswerId: 3,
      explanation: `
        <p><strong>正解は \\(\\lambda = 1, 4\\) です。</strong></p>
        <p>固有多項式 \\(\\det(B - \\lambda I) = 0\\) を計算します。</p>
        <p>$$ \\det \\begin{pmatrix} 2-\\lambda & -1 \\\\ -2 & 3-\\lambda \\end{pmatrix} = 0 $$</p>
        <p>
          行列式を計算すると、<br>
          \\((2-\\lambda)(3-\\lambda) - (-1) \\cdot (-2) = 0\\) <br>
          \\(\\lambda^2 - 5\\lambda + 4 = 0\\) <br>
          \\((\\lambda - 1)(\\lambda - 4) = 0\\)
        </p>
        <p>したがって、固有値は \\(\\lambda = 1, 4\\) となります。</p>
      `
    }
  ]
});
    
const selectedAnswers = reactive({});
const revealedAnswers = reactive({});

function revealAnswer(quizId) {
  revealedAnswers[quizId] = !revealedAnswers[quizId];
}

const appContainer = ref(null);

const typesetMath = () => {
  if (appContainer.value && window.MathJax && typeof window.MathJax.typesetPromise === 'function') {
    window.MathJax.typesetPromise([appContainer.value]);
  }
};

onMounted(() => {
  // 念のため、100ミリ秒待ってから実行
  setTimeout(typesetMath, 100);
});

onUpdated(() => {
  // 画面が更新されたら、すぐに数式変換を実行
  typesetMath();
});
</script>

